<template>
  <view class="page">
    <!-- Â§¥ÈÉ®ËÉåÊôØ -->
    <view class="header-bg">
      <view class="profile-card" @click="goPage('profile')">
        <view class="avatar-wrap">
          <image v-if="userInfo.avatar" :src="userInfo.avatar" class="avatar" mode="aspectFill" />
          <view v-else class="avatar"></view>
        </view>
        <view class="user-info">
          <text class="nickname">{{ userInfo.nickname }}</text>
          <text class="user-id">ËáªÊâòÂè∑: {{ userInfo.tfId }}</text>
        </view>
        <text class="edit-icon">‚Ä∫</text>
      </view>
    </view>

    <!-- ‰ø°Áî®ÂàÜ -->
    <view class="credit-card">
      <view class="credit-left">
        <text class="credit-icon">üèÜ</text>
        <text class="credit-label">ËáªÊâò‰ø°Áî®ÂàÜ</text>
      </view>
      <view class="credit-right">
        <text class="credit-score">{{ userInfo.creditScore }}</text>
        <text class="credit-level">{{ getCreditLevel(userInfo.creditScore) }}</text>
      </view>
    </view>

    <!-- ÂäüËÉΩËèúÂçï -->
    <view class="menu-card">
      <view class="menu-item" @click="goPage('wallet')">
        <text class="menu-icon">üí∞</text>
        <text class="menu-label">ÊàëÁöÑÈí±ÂåÖ</text>
        <text class="menu-arrow">‚Ä∫</text>
      </view>
      <view class="menu-item" @click="goPage('contract')">
        <text class="menu-icon">üìÑ</text>
        <text class="menu-label">ÊàëÁöÑÂêàÂêå</text>
        <text class="menu-arrow">‚Ä∫</text>
      </view>
      <view class="menu-item" @click="goPage('merchantApply')">
        <text class="menu-icon">üè™</text>
        <text class="menu-label">{{ merchantStatus.isMerchant ? 'ÂïÜÂÆ∂‰∏≠ÂøÉ' : 'Áî≥ËØ∑Êé•Âçï' }}</text>
        <text v-if="merchantStatus.hasPendingApply" class="badge">ÂÆ°Ê†∏‰∏≠</text>
        <text class="menu-arrow">‚Ä∫</text>
      </view>
    </view>

    <view class="menu-card">
      <view class="menu-item" @click="goPage('security')">
        <text class="menu-icon">üîê</text>
        <text class="menu-label">ÂÆâÂÖ®‰∏≠ÂøÉ</text>
        <text class="menu-arrow">‚Ä∫</text>
      </view>
      <view class="menu-item" @click="goPage('help')">
        <text class="menu-icon">‚ùì</text>
        <text class="menu-label">Â∏ÆÂä©‰∏éÂèçÈ¶à</text>
        <text class="menu-arrow">‚Ä∫</text>
      </view>
      <view class="menu-item" @click="goPage('about')">
        <text class="menu-icon">‚ÑπÔ∏è</text>
        <text class="menu-label">ÂÖ≥‰∫éËáªÊâò</text>
        <text class="menu-arrow">‚Ä∫</text>
      </view>
    </view>

    <!-- ÈÄÄÂá∫ÁôªÂΩï -->
    <view class="logout-btn" @click="handleLogout">
      ÈÄÄÂá∫ÁôªÂΩï
    </view>
    
    <!-- Ëá™ÂÆö‰πâTabBar -->
    <custom-tabbar :current="3"></custom-tabbar>
  </view>
</template>

<script>
import { useUserStore } from '@/stores/user'
import { request } from '@/utils/request'

export default {
  data() {
    return {
      userInfo: {
        nickname: '',
        tfId: '',
        creditScore: 0,
        avatar: ''
      },
      merchantStatus: {
        isMerchant: false,
        hasPendingApply: false
      },
      loading: false
    }
  },
  mounted() {
    this.loadUserInfo()
    this.loadMerchantStatus()
  },
  onShow() {
    // ÊØèÊ¨°ÊòæÁ§∫Êó∂Âà∑Êñ∞Áî®Êà∑‰ø°ÊÅØÂíåÂïÜÂÆ∂Áä∂ÊÄÅ
    this.loadUserInfo()
    this.loadMerchantStatus()
  },
  methods: {
    async loadUserInfo() {
      const userStore = useUserStore()
      // ÂÖà‰ªéÊú¨Âú∞Ëé∑Âèñ
      if (userStore.userInfo && userStore.userInfo.nickname) {
        this.userInfo = {
          nickname: userStore.userInfo.nickname || 'Áî®Êà∑',
          tfId: 'TF_' + (userStore.userInfo.id || '000000'),
          creditScore: userStore.userInfo.creditScore || 600,
          avatar: userStore.userInfo.avatar || ''
        }
      }
      // ‰ªéÂêéÁ´ØÂà∑Êñ∞ÊúÄÊñ∞Êï∞ÊçÆ
      try {
        this.loading = true
        const res = await request({
          url: '/auth/info',
          method: 'GET'
        })
        if (res.data) {
          this.userInfo = {
            nickname: res.data.nickname || 'Áî®Êà∑',
            tfId: 'TF_' + res.data.id,
            creditScore: res.data.creditScore || 600,
            avatar: res.data.avatar || ''
          }
          // Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®
          userStore.setUserInfo(res.data)
        }
      } catch (e) {
        console.error('Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•', e)
      } finally {
        this.loading = false
      }
    },
    async loadMerchantStatus() {
      try {
        const res = await request({
          url: '/merchant/status',
          method: 'GET'
        })
        if (res.data) {
          this.merchantStatus = {
            isMerchant: res.data.isMerchant || false,
            hasPendingApply: res.data.hasPendingApply || false
          }
        }
      } catch (e) {
        console.error('Âä†ËΩΩÂïÜÂÆ∂Áä∂ÊÄÅÂ§±Ë¥•', e)
      }
    },
    getCreditLevel(score) {
      if (score >= 750) return 'ÊûÅÂ•Ω'
      if (score >= 650) return '‰ºòÁßÄ'
      if (score >= 550) return 'ËâØÂ•Ω'
      if (score >= 350) return '‰∏≠Á≠â'
      return 'ËæÉÂ∑Æ'
    },
    goPage(page) {
      const routes = {
        profile: '/pages/user/profile',
        wallet: '/pages/wallet/index',
        merchantApply: '/pages/merchant/apply',
        contract: '/pages/user/contract',
        security: '/pages/user/security',
        help: '/pages/user/help',
        about: '/pages/user/about'
      }
      
      if (routes[page]) {
        uni.navigateTo({ url: routes[page] })
      } else {
        uni.showToast({ title: 'Âç≥Â∞ÜÂºÄÊîæ', icon: 'none' })
      }
    },
    handleLogout() {
      const userStore = useUserStore()
      uni.showModal({
        title: 'ÊèêÁ§∫',
        content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            userStore.logout()
            uni.reLaunch({ url: '/pages/login/index' })
          }
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.page {
  min-height: 100vh;
  background: #f0f4f8;
  padding-bottom: 180rpx;
}

.header-bg {
  background: linear-gradient(135deg, #00AFE1 0%, #0084A8 100%);
  height: 380rpx;
  position: relative;
}

.profile-card {
  position: absolute;
  bottom: -80rpx;
  left: 32rpx;
  right: 32rpx;
  display: flex;
  align-items: flex-end;
  gap: 24rpx;
}

.avatar-wrap {
  width: 192rpx;
  height: 192rpx;
  background: #fff;
  border-radius: 64rpx;
  padding: 8rpx;
  box-shadow: 0 8rpx 32rpx rgba(0, 0, 0, 0.1);
}

.avatar {
  width: 100%;
  height: 100%;
  background: #e5e5e5;
  border-radius: 56rpx;
  display: block;
}

.edit-icon {
  font-size: 48rpx;
  color: #999;
  padding-bottom: 16rpx;
}

.user-info {
  padding-bottom: 16rpx;
}

.nickname {
  font-size: 40rpx;
  font-weight: 900;
  color: #333;
  display: block;
}

.user-id {
  font-size: 26rpx;
  color: #999;
  margin-top: 8rpx;
}

.credit-card {
  margin: 120rpx 32rpx 32rpx;
  background: #fff;
  border-radius: 48rpx;
  padding: 40rpx;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4rpx 16rpx rgba(0, 0, 0, 0.05);
}

.credit-left {
  display: flex;
  align-items: center;
  gap: 16rpx;
}

.credit-icon {
  font-size: 48rpx;
}

.credit-label {
  font-size: 30rpx;
  font-weight: 600;
  color: #333;
}

.credit-right {
  display: flex;
  align-items: baseline;
  gap: 12rpx;
}

.credit-score {
  font-size: 48rpx;
  font-weight: 900;
  color: #00AFE1;
}

.credit-level {
  font-size: 24rpx;
  color: #22c55e;
  font-weight: 600;
}

.menu-card {
  margin: 0 32rpx 32rpx;
  background: #fff;
  border-radius: 48rpx;
  overflow: hidden;
  box-shadow: 0 4rpx 16rpx rgba(0, 0, 0, 0.05);
}

.menu-item {
  display: flex;
  align-items: center;
  padding: 40rpx;
  border-bottom: 2rpx solid #f5f5f5;
  
  &:last-child {
    border-bottom: none;
  }
}

.menu-icon {
  font-size: 40rpx;
  margin-right: 24rpx;
}

.menu-label {
  flex: 1;
