  margin-right: 16rpx;
}

.menu-arrow {
  font-size: 36rpx;
  color: #ccc;
}

.logout-btn {
  margin: 48rpx 32rpx;
  background: #fff;
  border-radius: 48rpx;
  padding: 36rpx;
  text-align: center;
  font-size: 30rpx;
  font-weight: 600;
  color: #ef4444;
  box-shadow: 0 4rpx 16rpx rgba(0, 0, 0, 0.05);
}
</style>
